.btn {
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  border: none;
  background: $color-primary;
  padding: 19px 30px;
  border-radius: 50px;
  max-width: 330px;
  outline: none;

  font-family: $font-display;
  font-size: 22px;
  line-height: 26px;
  color: $white;

  transition: transform $aos-duration-transform ease, opacity $aos-duration-opacity ease, 0.3s background-color;

  &:hover,
  &:focus {
    transition-duration: 0.1s;
    background-color: $color-primary-dark;
  }

  @include mobile {
    max-width: 240px;
    font-size: 14px;
    line-height: 17px;
    padding: 14px 30px;
  }
}

.btn--light {
  background: rgba(0, 41, 90, 0.05);
  padding: 12px 30px;

  font-size: 17px;
  line-height: 24px;
  color: $color-blue;

  transition: 0.3s background-color, 0.3s color;

  &:hover,
  &:focus {
    background-color: $color-blue-dark;
  }
}

.btn--with-arrow {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 260px;

  svg {
    stroke: $color-blue;
    transition: stroke 0.3s;
    margin-left: 8px;
    margin-top: 2px;
  }

  @include mobile {
    font-size: 17px;
    line-height: 21px;
    background: none;
    padding: 12px 20px;

    &:hover,
    &:focus {
      background: none;
    }
  }
}
