.landing-footer {
  font-size: 16px;
  line-height: 19px;
  background: $white;
  color: $color-blue;

  font-family: $font-family-mac;

  @include tablet {
    text-align: center;
  }
}

.landing-footer__container {
  @include container;
  padding-top: 32px;
  padding-bottom: 32px;

  display: flex;

  @include tablet {
    padding-top: 24px;
    padding-bottom: 24px;
    flex-direction: column;
    align-items: center;
  }
}

.landing-footer__section {
  margin-right: 120px;

  &:nth-child(2n) {
    margin-right: 100px;
  }

  &:nth-child(3n) {
    width: 200px;
    margin-right: 50px;
  }

  &:nth-child(4n) {
    margin-left: auto;
    margin-right: 0;
  }

  @include tablet {
    width: auto;
    margin: 0;

    &--last-on-tablet {
      order: 2;
    }
  }
}

.landing-footer__logo {
  display: block;

  img {
    vertical-align: top;
  }

  @include tablet {
    width: 115px;
    padding-top: 24px;
  }
}
