.cases {
  padding: 45px 20px 75px;
  text-align: center;

  @include mobile {
    padding: 45px 16px;
  }
}

.cases__count {
  @include title;
  margin: 0 0 2px;

  @include mobile {
    margin-bottom: 8px;
  }
}

.cases__desc {
  @include subtitle;

  color: $gray-dark;
  margin: 0;

  @include mobile {
    line-height: 1.2;
  }
}

.cases__list {
  margin: 0 -20px;

  .flickity-slider {
    will-change: transform;
    transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
  }
}

.cases__item {
  padding: 55px 30px;

  @include mobile {
    padding: 30px 8px;
  }
}

.cases__link {
  display: block;
  width: 90px;
  height: 90px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  overflow: hidden;

  transition: box-shadow 0.3s;

  &:hover,
  &:focus {
    box-shadow: 0 10px 22px rgba(0, 0, 0, 0.1);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    vertical-align: top;
  }

  @include mobile {
    width: 60px;
    height: 60px;
  }
}

.cases__btn {
  @include mobile {
    max-width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}
