.nd-vkbiz-nav {
  display: flex;
  align-items: center;
  flex-grow: 1;
  text-align: left;
  font-family: $font-family-roboto;
  font-size: 16px;
  line-height: 19px;
  color: #4680c2;
  letter-spacing: -0.4px;
  transition: all 0.3s ease;
}

.nd-vkbiz-nav__back-link {
  position: relative;
  box-sizing: border-box;
  padding: 13px 40px 19px;
  width: 100%;
  text-align: left;
  font-family: $font-family-roboto;
  font-size: 16px;
  line-height: 20px;
  color: #4680c2;
  background-color: #ffffff;
  border: 0;
  display: none;
}

.mac {
  .nd-vkbiz-nav,
  .nd-vkbiz-nav__back-link {
    font-family: $font-family-mac;
  }
}

.nd-vkbiz-nav__back-link::before,
.nd-vkbiz-nav__back-link::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 2px;
  background-color: #4680c2;
  border-radius: 2px;
  transform: rotate(45deg);
  pointer-events: none;
}

.nd-vkbiz-nav__back-link::before {
  left: 20px;
  top: 50%;
  margin-top: 0;
}

.nd-vkbiz-nav__back-link::after {
  left: 20px;
  bottom: 50%;
  margin-bottom: 4px;
  transform: rotate(135deg);
}

.nd-vkbiz-nav__back-link.hidden {
  display: none;
}

ul.nd-vkbiz-nav__list {
  display: flex;
  margin: 0 auto;
  margin-right: 0;
  padding: 0;
  list-style: none;
}

.nd-vkbiz-nav__list-item {
  position: relative;
}

.nd-vkbiz-nav__list-item:hover .nd-vkbiz-nav__list-link {
  background-color: #f6f8f9;
}

.nd-vkbiz-nav__list-item_hidden {
  display: none;
}

.nd-vkbiz-nav__list-link {
  display: inline-block;
  vertical-align: top;
  padding: 5px 16px 4px;
  color: #4680c2;
  border-radius: 15px;
  transition: background-color 0.3s ease;
}

.nd-vkbiz-nav__list-link:hover {
  text-decoration: none;
  background-color: #f6f8f9;
}

.nd-vkbiz-nav__list-link:hover::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  height: 8px;
}

.nd-vkbiz-nav__list-link:hover + ul.nd-vkbiz-subnav {
  opacity: 1;
  pointer-events: auto;
}

@media (max-width: 1039px) {
  .nd-vkbiz-nav__list-item:last-child,
  .nd-vkbiz-nav__list-item:nth-last-child(2) {
    display: none;
  }

  .nd-vkbiz-nav__list-item_hidden {
    display: block;
  }
}

@media (max-width: 879px) {
  .nd-vkbiz-nav {
    position: fixed;
    top: 56px;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateX(-100%);
    display: flex;
    flex-direction: column;
    padding-top: 2px;
    background-color: #ffffff;
    overflow-y: auto;
  }

  .nd-vkbiz-nav.overflow {
    overflow: hidden;
  }

  ul.nd-vkbiz-nav__list {
    flex-direction: column;
    justify-content: flex-start;
    margin: 0;
    box-sizing: border-box;
    width: 100%;
  }

  .nd-vkbiz-nav__list-item_has-subnav_illustrated .nd-vkbiz-nav__list-link {
    position: relative;
  }

  .nd-vkbiz-nav__list-item_has-subnav_illustrated .nd-vkbiz-nav__list-link::before,
  .nd-vkbiz-nav__list-item_has-subnav_illustrated .nd-vkbiz-nav__list-link::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 2px;
    background-color: #4680C2;
    border-radius: 2px;
    transform: rotate(45deg);
    pointer-events: none;
  }

  .nd-vkbiz-nav__list-item_has-subnav_illustrated .nd-vkbiz-nav__list-link::before {
    right: 20px;
    bottom: 50%;
    margin-bottom: 2px;
  }

  .nd-vkbiz-nav__list-item_has-subnav_illustrated .nd-vkbiz-nav__list-link::after {
    right: 20px;
    top: 50%;
    margin-top: 2px;
    transform: rotate(135deg);
  }

  .nd-vkbiz-nav__list-link {
    box-sizing: border-box;
    padding: 14px 16px;
    width: 100%;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.5px;
    border-radius: 0;
  }

  .nd-vkbiz-nav__list-item_has-subnav .nd-vkbiz-nav__list-link {
    display: none;
  }

  .nd-vkbiz-nav__list-item_has-subnav_illustrated .nd-vkbiz-nav__list-link {
    display: block;
  }

  .nd-vkbiz-nav__list-link:hover::after {
    right: 20px;
    left: auto;
    top: 50%;
  }

  .nd-vkbiz-nav__back-link:not(.hidden) {
    display: flex;
    align-items: center;
    background-color: #fff;
    z-index: 1;
  }

  .nd-vkbiz-nav__back-link:not(.hidden) + .nd-vkbiz-nav__list {
    margin-top: -52px;
  }

  .nd-vkbiz-subnav_illustrated li.nd-vkbiz-subnav__item {
    margin-bottom: 19px;
  }

  .nd-vkbiz-nav--old {
    width: 232px;
    top: 55px;
    right: 16px;
    left: auto;
    bottom: auto;
    opacity: 0;
    padding-top: 0;

    transition: opacity 0.25s ease, transform 0.25s ease;
    transform: translateY(10px);

    pointer-events: none;

    border-radius: 12px;
    background-color: #ffffff;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.15);

    .nd-vkbiz-nav__list-item:not(:last-of-type) {
      border-bottom: 1px solid #eaf1f8;
    }

    .nd-vkbiz-nav__list-link {
      padding: 20px;
    }

    .nd-vkbiz-nav__list-item:last-child,
    .nd-vkbiz-nav__list-item:nth-last-child(2) {
      display: block;
    }

    .nd-vkbiz-header__user-block {
      display: none;
    }
  }

  header.nd-vkbiz-header.menu-opened .nd-vkbiz-nav--old {
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0);
  }
}
