.main-nav {
  position: fixed;
  right: 47px;
  z-index: 900;
  top: 50%;

  transform: translate3d(0, -50%, 0);
  transform-style: preserve-3d;
  will-change: transform;
  backface-visibility: hidden;

  @include laptop {
    display: none;
  }
}

@supports (position: sticky) {
  .main-nav {
    position: sticky;
    right: 0;
    width: 40px;
    margin-left: auto;
    margin-right: 47px;
    height: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include laptop {
      display: none;
    }
  }
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.main-nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin: 5px;
  position: relative;
  z-index: 1;
}

.main-nav__link {
  display: block;
  cursor: pointer;
  text-decoration: none;

  border-radius: 50%;
  min-height: 17px;
  min-width: 17px;
  border: 0;
  background: #5c9ce6;
  opacity: 0.3;
  transition: opacity 0.1s linear, min-height 0.1s linear, min-width 0.1s linear;

  &:hover {
    min-height: 25px;
    min-width: 25px;
    opacity: 1;
  }
}

.main-nav__item--active .main-nav__link {
  min-height: 30px;
  min-width: 30px;
  opacity: 1;
}
