.schedule-card {
  padding: 46px 32px 40px;
  background: linear-gradient(180deg, #000000 7.44%, #000194 62.09%, #00ebf5 109.53%), #ffffff;
  border-radius: 20px;
  color: $white;
  overflow: hidden;
  min-height: 265px;

  display: flex;
  flex-direction: column;

  width: 100%;
  flex-shrink: 0;
  max-width: 194px;
  margin-right: 24px;

  &--active-hiring,
  &--active-members {
    max-width: 346px;
    min-height: 383px;
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.12);
  }

  &--new {
    background: #f8fafd;
    color: #abb6c6;
    padding: 32px 15px;
    padding-top: 50px;
    text-align: center;
  }

  &--finished,
  &--finished-results {
    text-align: center;
    padding: 32px 16px 32px;
    color: rgba($white, 0.5);
  }

  &--finished-results {
    padding: 32px 24px 0;
    text-align: left;
    max-width: 316px;
    min-height: 383px;
  }

  @include mobile {
    padding: 24px;
    padding-bottom: 16px;
    box-shadow: none;
    max-width: 232px;
    min-height: 265px;
    border-radius: 24px;
    margin-right: 16px;

    &--finished-results {
      padding-bottom: 0;
    }

    &--finished {
      padding: 32px;
    }

    &--new {
      padding: 32px 10px;
      padding-top: 50px;
      max-width: 194px;
    }

    &--active-hiring,
    &--active-members {
      padding: 31px 24px 16px;
    }
  }
}

.schedule-card__members {
  margin-top: auto;
}

.schedule-card__title {
  font-size: 16px;
  line-height: 1.25;
  margin: 0 0 25px;
  font-weight: 700;
  letter-spacing: 0.3px;
  color: $white;
  font-family: inherit;

  &--large {
    margin-bottom: 20px;
    font-size: 26px;
    line-height: 1.2;
    font-family: $font-commons;
    font-weight: 500;
    letter-spacing: 0;
  }

  &--medium {
    font-size: 24px;
    line-height: 1.2;
    font-family: $font-commons;
    font-weight: 500;
    letter-spacing: 0;
    margin-bottom: 16px;
  }

  @include mobile {
    font-size: 15px;
    line-height: 1.4;
    font-family: inherit;
    font-weight: 700;

    &--large {
      margin-bottom: 16px;
    }

    &--medium {
      font-weight: 600;
    }
  }
}

.schedule-card__details {
  font-size: 16px;
  margin: 0 0 20px;
  letter-spacing: 0.15px;
  color: $white;
  font-weight: 600;

  span:not(:last-child) {
    &::after {
      content: "  ";
      color: #8080c9;
    }
  }

  @include mobile {
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 8px;
    font-weight: 700;

    span:not(:last-child) {
      display: block;

      &::after {
        display: none;
      }
    }
  }
}

.schedule-card__link {
  align-self: flex-start;
  color: rgba($white, 0.8);
  font-size: 14px;
  line-height: 1;
  border-bottom: 1px solid rgba($white, 0.2);
  transition: 0.3s border-color;

  &:hover,
  &:focus {
    border-bottom-color: rgba($white, 0);
  }

  @include mobile {
    font-size: 13px;
  }
}

.schedule-card__text {
  margin-bottom: auto;
}

.schedule-card__btn {
  color: $white;
  background: rgba(255, 255, 255, 0.2);
  font-size: 18px;
  line-height: 20px;
  padding: 14px 30px;
  display: block;
  text-align: center;
  margin-top: auto;
  margin-bottom: 16px;

  &:hover,
  &:focus {
    background-color: rgba(255, 255, 255, 0.3);
  }

  @include mobile {
    font-size: 14px;
    padding: 8px 20px;
    margin-bottom: 8px;
  }
}

.schedule-card__call {
  color: rgba($white, 0.8);
  font-size: 14px;
  line-height: 1;
  text-align: center;
  margin: 0;

  @include mobile {
    font-size: 12px;
  }
}

.schedule-card__status {
  margin: 0 0 8px;
  font-size: 14px;
  line-height: 1;
  font-weight: 500;

  @include mobile {
    margin-bottom: 8px;
  }
}

.schedule-card__date {
  margin: 24px 0 7px;
  font-size: 18px;
  line-height: 1.3;

  &--lock,
  &--clock {
    &::before {
      content: "";
      display: block;
      width: 56px;
      height: 56px;
      margin: 0 auto 16px;
      background: url("../img/make-biz/lock.svg") no-repeat;
      background-size: 100%;
    }
  }

  &--clock {
    margin-top: auto;
    margin-bottom: 0;
    font-size: 16px;
    line-height: 1.3;
    font-weight: 500;
    color: $white;

    &::before {
      width: 40px;
      height: 40px;
      background-image: url("../img/make-biz/clock.svg");
    }

    strong {
      display: block;
      font-weight: 500;
    }
  }

  strong {
    font-weight: 400;
    letter-spacing: 1px;
  }
}

.schedule-card__winners {
  margin: auto -24px 0;

  @include mobile {
    margin-left: -24px;
    margin-right: -24px;
  }
}

.schedule-card__hiring {
  margin-top: auto;
}
