.vertical-menu {
  padding: 8px 0;
}

.vertical-menu__title {
  margin: 0 0 17px;
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
}

.vertical-menu__list {
  @include list-reset;
}

.vertical-menu__item {
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }

  @include tablet {
    margin-bottom: 17px;
  }
}

.vertical-menu__link {
  display: inline-flex;
  align-items: center;

  color: $color-blue;

  span {
    @include text-link;
  }

  &:hover span::after,
  &:focus span::after {
    opacity: 1;
  }

  svg {
    width: 5px;
    height: 9px;
    stroke: currentColor;
    margin-left: 6px;
  }
}
