.news {
  @include container;
  text-align: center;
  padding-top: 80px;
  padding-bottom: 80px;

  @include mobile {
    padding: 38px 16px 60px;
  }
}

.news__title {
  @include title;
  margin: 0 0 30px;

  @include mobile {
    margin-bottom: 22px;
  }
}

.news__tag {
  @include text-link;
}

.news__tabs-nav {
  margin-bottom: 50px;

  @include mobile {
    margin-bottom: 65px;
  }
}

.news__list {
  display: flex;
  flex-wrap: wrap;
  margin-right: -26px;
  margin-bottom: -26px;

  @include tablet {
    margin: 0;
  }
}

.news__item {
  display: block;
  position: relative;
  width: 316px;
  margin-right: 26px;
  margin-bottom: 26px;
  color: inherit;
  text-decoration: none;

  background: $white;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.04);
  border-radius: 10px;
  overflow: hidden;

  transition: box-shadow 0.3s;

  &:hover {
    box-shadow: 0 6px 24px rgba(0, 0, 0, 0.1);
  }

  @include tablet {
    width: 274px;
    margin: 0;
  }
}

.news__tabs {
  margin-bottom: 55px;

  @include mobile {
    margin-bottom: 60px;
  }
}

.news__others {
  margin: 0;

  font-size: 18px;
  line-height: 1;

  a {
    @include text-link;
  }

  @include mobile {
    font-size: 16px;
    line-height: 1.3;
  }
}

.news__media {
  position: relative;
  overflow: hidden;

  img {
    width: 100%;
    height: auto;
    vertical-align: top;
  }
}

.news__text {
  padding: 16px;
  text-align: left;
}

.news__date {
  margin: 0;

  font-size: 13px;
  line-height: 1.3;
  color: #939393;

  margin-bottom: 5px;
}

.news__desc {
  margin: 0;
  font-size: 14px;
  line-height: 1.3;
  color: $black;

  @include mobile {
    font-size: 14px;
  }
}
