.members__title {
  margin: 0 0 16px;
  font-size: 14px;
  line-height: 1;
  color: rgba(236, 219, 247, 0.5);
}

.members__list {
  @include list-reset;

  display: flex;

  &--centred {
    justify-content: center;
  }
}

.members__item {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;

  margin-right: -10px;

  &--medium {
    width: 52px;
    height: 52px;
    margin-right: -12px;
  }

  @include mobile {
    width: 39px;
    height: 39px;

    &--medium {
      width: 52px;
      height: 52px;
    }
  }
}

.members__link {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #019cd5;

  img {
    vertical-align: top;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &--more {
    background: #3ebeed;
    border: 2px solid #019cd5;
    font-size: 14px;
    line-height: 1;
    text-align: center;
    color: $white;

    display: flex;
    justify-content: center;
    align-items: center;

    @include mobile {
      font-size: 14px;
    }
  }

  &--light {
    border-color: $white;
    background-color: #bec4d6;
  }
}
