.winners {
  position: relative;
}

.winners__title {
  margin: 0;
  position: absolute;
  bottom: 36px;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 18px;
  line-height: 1.25;
  color: $white;
  padding: 0 14px;
  font-weight: 700;

  @include mobile {
    font-size: 14px;
    bottom: 18px;
  }
}

.winners__list {
  @include list-reset;

  display: flex;
  align-items: flex-end;
  padding: 0 24px;

  @include mobile {
    padding: 0 18px;
  }
}

.winners__item {
  width: 48px;
  height: 207px;

  margin-right: 6px;
  background: rgba(255, 255, 255, 0.1);
  border-top-left-radius: 36px;
  border-top-right-radius: 36px;

  &:last-child {
    margin-right: 0;
  }

  &--2-place {
    height: 186px;
  }

  &--3-place {
    height: 165px;
  }

  &--4-place {
    height: 144px;
  }

  &--5-place {
    height: 123px;
  }

  @include mobile {
    width: 36px;
    height: 116px;

    &--2-place {
      height: 105px;
    }

    &--3-place {
      height: 94px;
    }

    &--4-place {
      height: 83px;
    }

    &--5-place {
      height: 72px;
    }
  }
}

.winners__link {
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    vertical-align: top;
  }

  @include mobile {
    width: 36px;
    height: 36px;
  }
}
