.about {
  padding-top: 60px;

  @include mobile {
    padding-top: 56px;
  }
}

.about__container {
  @include container;
  padding-top: 115px;
  padding-bottom: 58px;

  @include mobile {
    padding: 48px 16px;
  }
}

.about__title {
  margin: 0 0 14px;
  text-transform: uppercase;
  font-family: $font-commons;
  font-size: 0;
  font-weight: 800;

  @include mobile {
    margin-bottom: 5px;

    opacity: 0;

    &--show {
      opacity: 1;
    }
  }
}

.about__title-text {
  font-size: 112px;
  line-height: 1;

  @include mobile {
    font-size: 65px;
    line-height: 57px;
  }

  &--normal {
    font-weight: 700;
    margin-left: -7px;
    letter-spacing: -0.5px;

    @include mobile {
      margin-left: -4px;
    }
  }

  @supports (-webkit-background-clip: text) {
    // background: linear-gradient(153.79deg, $color-violet 29.84%, #270f75 63%, #64168d 99.8%);
    background: linear-gradient(164.45deg, #000194, #00ebf5);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    box-decoration-break: clone;

    @include mobile {
      background-image: linear-gradient(133.59deg, #000194 47%, #00ebf5);
    }

    &--dark {
      background-image: linear-gradient(164.45deg, #010183 0%, #000194 100%);
    }

    &--outline {
      background: none;
      -webkit-text-stroke: 2px #000194;
    }
  }
}

.about__desc,
.about__call {
  margin: 0;
}

.about__desc {
  font-size: 50px;
  line-height: 1.2;
  font-family: $font-commons;
  font-weight: 600;
  max-width: 800px;
  margin-bottom: 14px;

  @supports (-webkit-background-clip: text) {
    background: linear-gradient(120deg, #000000 0%, #000194 47.4%, #00EBF5 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    box-decoration-break: clone;
  }


  @include mobile {
    font-size: 26px;
    margin-bottom: 13px;
  }
}

.about__call {
  font-size: 18px;
  line-height: 1.25;
  color: $black;
  max-width: 630px;
  margin-bottom: 96px;

  a {
    @include text-link;
  }

  @include mobile {
    font-size: 14px;
    margin-bottom: 40px;
    color: rgba(11, 16, 81, 0.4);

    br {
      display: none;
    }
  }
}

.about__btn {
  @include btn-reset;

  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 2px;
  width: 48px;
  height: 48px;
  border: 2px solid currentColor;
  border-radius: 50%;
  transition: 0.3s opacity;
  color: $black-dark;
  margin-left: auto;
  margin-right: auto;

  outline: none;

  svg {
    fill: currentColor;
  }

  &:hover,
  &:focus {
    opacity: 0.5;
  }

  @include mobile {
    display: none;
  }
}

.about__link {
  display: none;
  border: 1.5px solid currentColor;
  border-radius: 100px;
  color: $color-violet;

  font-weight: 500;
  font-size: 18px;
  line-height: 20px;
  font-family: inherit;
  letter-spacing: 0.3px;

  padding: 12px 30px;
  text-align: center;

  @include mobile {
    display: block;
  }
}
