.subscribe {
  background-color: $white;
  background-size: 100%, 100%;
  padding: 124px 20px 67px;
  color: $color-primary-dark;
  text-align: center;
  flex-grow: 1;
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include laptop {
    background: url("../img/wave2.svg") no-repeat center top, linear-gradient($white, $white) no-repeat center 300px;
  }

  &::before {
    content: "";
    display: block;
    width: 153px;
    height: 153px;
    background: url("../img/man@1x.png") no-repeat;
    background-size: 100%;
    margin: 0 auto 24px;
  }

  @include retina {
    &::before {
      background-image: url("../img/man@2x.png");
    }
  }

  @include mobile {
    padding-top: 175px;
    background-image: url("../img/wave-mobile.svg"), linear-gradient($white, $white);
    background-size: 100% auto, 100%;

    &::before {
      width: 139px;
      height: 139px;
      margin-bottom: 16px;
    }
  }
}

.subscribe__hint {
  bottom: auto;
  top: -28px;

  &::after {
    bottom: auto;
    top: -80px;
  }

  .bubble__text {
    border: 1px solid #e0e0e0;
  }

  .bubble__text::before {
    bottom: auto;
    top: -23px;
    filter: none;
    background: url("../img/bubble-top.png");
    transform: rotate(180deg) scale(-1, 1);
    width: 30px;
    height: 23px;
    background-size: 100%;
  }

  @include laptop {
    top: 78px;
  }
}

.subscribe__title {
  margin: 0 auto 35px;

  font-family: $font-commons;
  font-size: 60px;
  line-height: 1;

  max-width: 600px;

  @include mobile {
    font-size: 28px;
    margin-bottom: 14px;
  }
}

.subscribe__question {
  padding: 19px 40px;
  padding-right: 60px;
  min-width: 173px;

  background: $color-primary url("../img/toRight.svg") no-repeat right 32px center;

  @include mobile {
    padding: 14px 38px;
    padding-right: 58px;
    min-width: 138px;
    background-size: 7px 12px;
    background-position: right 27px center;
  }
}

.section:last-child .fp-tableCell {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.fp-scroller {
  min-height: 100%;
}
