header.nd-vkbiz-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 14px 0 30px;
  max-width: 100vw;
  font-family: $font-family-roboto;
  z-index: 100;
  transition: all 0.3s ease;

  padding-bottom: 14px;
  background-color: #ffffff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
}

.nd-vkbiz-header__wrapper {
  display: flex;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  max-width: 1840px;
}

