.nd-vkbiz-header__toggler {
  position: relative;
  display: none;
  margin-left: auto;
  padding: 0;
  width: 18px;
  height: 14px;
  background-color: rgba(255, 255, 255, 0);
  border: 0;
  border-radius: 0;
}

.nd-vkbiz-header__toggler::before,
.nd-vkbiz-header__toggler::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #4680c2;
  transition: all 0.3s ease;
  pointer-events: none;
}

.nd-vkbiz-header__toggler::before {
  top: 0;
  box-shadow: 0 6px 0 0 #4680c2;
}

.nd-vkbiz-header__toggler::after {
  bottom: 0;
}

.nd-vkbiz-header.menu-opened .nd-vkbiz-header__toggler::before,
.nd-vkbiz-header.menu-opened .nd-vkbiz-header__toggler::after {
  top: 6px;
  left: 2px;
  transform: rotate(45deg);
  pointer-events: none;
}

.nd-vkbiz-header.menu-opened .nd-vkbiz-header__toggler::before {
  box-shadow: none;
}

.nd-vkbiz-header.menu-opened .nd-vkbiz-header__toggler::after {
  bottom: auto;
  transform: rotate(-45deg);
}

@media (max-width: 1039px) {
  header.nd-vkbiz-header {
    padding: 18px 0 12px;
  }

  .nd-vkbiz-header__wrapper {
    padding: 0 15px 0 26px;
  }

  .nd-vkbiz-header__logo {
    margin-right: 10px;
  }

  .nd-vkbiz-subnav__link_plain {
    letter-spacing: 0.5px;
  }
}

@media (max-width: 879px) {
  header.nd-vkbiz-header {
    padding: 0;
  }

  .nd-vkbiz-landing .nd-vkbiz-header {
    padding: 0;
  }

  header.nd-vkbiz-header.menu-opened,
  header.nd-vkbiz-header.menu-opened.scrolling {
    background-color: #ffffff;
    box-shadow: none;
  }

  header.nd-vkbiz-header.menu-opened .nd-vkbiz-header__nav {
    transform: translateX(0);
  }

  .nd-vkbiz-header__wrapper {
    padding: 0;
  }

  .nd-vkbiz-header__top {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 16px 21px 16px 16px;
    width: 100%;
  }

  .nd-vkbiz-header__logo {
    width: 116px;
    height: 24px;
  }

  .nd-vkbiz-header__toggler {
    display: block;
  }

  .nd-vkbiz-header__user-block {
    margin-top: auto;
    box-sizing: border-box;
    padding: 14px 16px;
    width: 100%;
    border-top: 1px solid #f6f8f9;
    border-radius: 0;
    letter-spacing: 0.3px;
  }
}
