.intro {
  position: relative;
  height: 100vh;
  min-height: 800px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  margin: 0 auto;
  padding-bottom: 100px;
}

.intro__logo-link {
  display: block;

  margin: 0;
  margin-top: 50px;
  margin-bottom: auto;
  padding: 0;
}

.loaded .intro__logo-link {
  transition: transform $aos-duration-transform ease, opacity $aos-duration-opacity ease;
}

.intro__logo-link:hover,
.intro__logo-link:focus {
  opacity: 0.5;
}

.intro__logo-link img {
  width: 193px;
  height: 39px;
}

.intro__title {
  position: relative;

  display: block;

  max-width: 961px;

  margin: 0 auto;
  margin-bottom: auto;
  padding: 90px 0;

  font-family: $font-commons;
  font-weight: 800;
  font-size: 90px;
  line-height: 1;
  text-align: center;
}

.intro__title::before {
  content: "";

  position: absolute;
  top: 0;
  left: 441px;

  width: 60px;
  height: 60px;

  border-radius: 50%;

  background-image: url("../img/title-check.svg");
  background-color: $color-secondary;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 35px auto;
}

.intro__title--second {
  display: none;
}

.intro__hint.bubble {
  position: absolute;
  bottom: 185px;
  left: 50%;
}

.intro__hint.bubble--right .bubble__text::before {
  bottom: -22px;

  background-image: url("../img/bubble.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  filter: drop-shadow($bubble-drop-shadow);

  transform: rotate(0deg) scale(1);
}

@include laptop {
  .intro__title {
    max-width: 700px;
    font-size: 80px;
  }

  .intro__title::before {
    left: 50%;
    transform: translateX(-50%);
  }

  .intro__hint.bubble {
    bottom: 40px;
  }
}

@include tablet {
  .intro {
    margin-bottom: 48px;
    height: calc(100vh - 135px);
  }

  .intro__logo-link {
    display: block;

    margin: 0;
    margin-top: 34px;
    margin-bottom: 50px;
    padding: 0;
  }

  .intro__logo-link img {
    width: 147px;
    height: 31px;
  }

  .intro__title {
    display: block;

    max-width: 332px;

    margin: 0 auto;
    padding: 0;
    padding-top: 76px;

    font-family: $font-commons;
    font-weight: 800;
    font-size: 44px;
    line-height: 1;
    text-align: center;
  }

  .intro__title::before {
    content: "";

    position: absolute;
    top: 0;
    left: 137px;
    transform: none;

    width: 58px;
    height: 58px;

    border-radius: 50%;

    background-image: url("../img/title-check.svg");
    background-color: $color-secondary;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 35px 30px;
  }

  .intro__title--second {
    display: none;
  }

  .intro__mobile-hidden {
    display: none;

    @include min-width {
      display: none;
    }
  }

  .intro__hint.bubble {
    position: absolute;
    bottom: -10px;
    left: 50%;
  }
}

@include mobile {
  .intro {
    height: calc(100vh - 135px);
    min-height: 400px;
  }
}
