@mixin desktop-2k {
  @media (min-width: $width-desktop-2k) {
    @content;
  }
}

@mixin desktop-large {
  @media (min-width: $width-desktop-large) {
    @content;
  }
}

@mixin desktop-only {
  @media (min-width: $width-desktop) {
    @content;
  }
}

@mixin laptop {
  @media (max-width: $width-desktop - 1px) {
    @content;
  }
}

@mixin laptop-only {
  @media (max-width: $width-desktop - 1px) and (min-width: $width-laptop) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: $width-laptop - 1px) {
    @content;
  }
}

@mixin tablet-only {
  @media (max-width: $width-laptop - 1px) and (min-width: $width-tablet) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: $width-tablet - 1px) {
    @content;
  }
}

@mixin min-width {
  @media (max-width: $width-min) {
    @content;
  }
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin container {
  width: 100%;
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 12px;
  padding-right: 12px;
}

@mixin list-reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

@mixin text-link($color: $color-blue) {
  color: $color;
  position: relative;
  padding-bottom: 1px;

  &::after {
    content: "";
    position: absolute;
    z-index: 1;
    left: 0;
    right: 0;
    bottom: 0;
    background: currentColor;
    opacity: 0;
    transition: 0.25s opacity ease;
    height: 1px;
  }

  &:hover::after,
  &:focus::after {
    opacity: 1;
  }
}

@mixin multilineUnderline($color: $color-blue) {
  background-image: linear-gradient($color, $color);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 100% 1px;
  transition: background-size 0.3s;

  &:hover,
  &:focus {
    background-size: 0% 1px;
  }
}

@mixin btn-reset {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
}

@mixin title {
  font-size: 40px;
  line-height: 1.3;
  font-family: $font-commons;
  font-weight: 600;

  @include mobile {
    font-size: 24px;
    line-height: 1.2;
  }
}

@mixin subtitle {
  font-size: 18px;
  line-height: 1;
  font-weight: 400;
  color: #abb6c6;;

  @include mobile {
    font-size: 14px;
  }
}

@mixin h1 {
  font-family: $font-commons;
  font-style: normal;
  font-weight: 500;
  font-size: 50px;
  line-height: 57px;

  @media (max-width: $mobile-width) {
    font-size: 29px;
    line-height: 33px;
  }
}

@mixin h2 {
  font-family: $font-commons;
  font-style: normal;
  font-weight: 500;
  font-size: 45px;
  line-height: 51px;

  @media (max-width: $mobile-width) {
    font-size: 26px;
    line-height: 30px;
  }
}
