html {
  box-sizing: border-box;
}

html,
body {
  @include mobile {
    height: 100%;
    width: 100%;
    overflow: auto;
  }

}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  min-width: $width-min;

  margin: 0;
  padding: 0;

  font-family: $font-text;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.5;
  color: $white;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

[data-aos="fadeup"] {
  transform: translate3d(0, 50px, 0);
  opacity: 0;
  pointer-events: none;

  &.aos-animate {
    transform: none;
    opacity: 1;
    pointer-events: auto;
  }
}

.loaded [data-aos="fadeup"] {
  transition: transform $aos-duration-transform ease, opacity $aos-duration-opacity ease;
}

.iframe-wrap {
  width: 100%;
  overflow: visible;

  iframe {
    width: 100%;
    height: 100vh;
    border: none;
  }
}

.overflow {
  overflow: hidden;
}

.full-page-wrap {
  width: 100%;
  height: 100vh;
  overflow: auto;
  -webkit-overflow-scrolling: touch;

  background-color: $color-primary;
  background-image: $bg-gradient;
}

body.nd-overflow {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.hide-on-tablet {
  @include tablet {
    display: none;
  }
}

.hide-on-desktop {
  @media (min-width: $width-laptop) {
    display: none;
  }
}

.main-content {
  position: relative;
  z-index: 1;
  overflow: hidden;
  color: $black;
  font-family: $font-family-roboto;
  font-size: 16px;
  background: linear-gradient(to bottom, #f7f9fd 0%, rgba(247, 249, 253, 0) 40%, $white 40%, $white 52%, #f7f9fd 82%, #f7f9fd 82%, #f7f9fd 100%) no-repeat;
  background-color: $white;

  @include laptop {
    background: linear-gradient(to bottom, #f7f9fd 0%, rgba(247, 249, 253, 0) 30%, $white 30%, $white 52%, #f7f9fd 82%, #f7f9fd 82%, #f7f9fd 100%) no-repeat;
    background-color: $white;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    z-index: -1;

    width: 1082px;
    height: 1232px;
    background: url("../img/make-biz/line.svg") no-repeat;
    background-size: 100%;
    top: 0;
    left: calc(50% - 720px);

    @include laptop {
      left: calc(50% - 635px);
    }

    @include tablet {
      display: none;
    }
  }

  &::after {
    width: 930px;
    height: 1033px;
    background-image: url("../img/make-biz/line-2.svg");
    top: auto;
    bottom: 316px;
    left: calc(50% + 7px);
    transform: translateX(-50%);
  }
}

.mac .main-content {
  font-family: $font-family-mac;
}
