.final-forum {
  @include container;
  text-align: center;
  position: relative;
  z-index: 1;
  padding: 197px 20px 285px;

  @include tablet {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @include mobile {
    padding: 60px 16px 50px;
  }
}

.final-forum__date {
  margin: 0 0 15px;
  font-size: 28px;
  line-height: 1;
  color: $gray-dark;
  font-weight: 600;
  font-family: $font-commons;

  &::before {
    content: "";
    display: block;
    width: 56px;
    height: 56px;
    margin: 0 auto 8px;
    background: url("../img/icon-calendar.svg") no-repeat;
  }

  @include mobile {
    padding-left: 10px;
    font-size: 24px;
    margin-bottom: 10px;
  }
}

.final-forum__title {
  @include title;
  margin: 0 0 35px;

  @include mobile {
    padding-left: 10px;
    margin-bottom: 27px;
  }
}

.final-forum__desc {
  font-size: 20px;
  line-height: 1.4;
  max-width: 650px;
  margin: 0 auto;
  position: relative;
  z-index: 2;

  a {
    @include text-link;
  }

  p {
    margin: 0;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include laptop {
    margin-bottom: 42px;
  }

  @include mobile {
    margin-bottom: 40px;
    font-size: 14px;
    color: #0B1051;
  }
}

.final-forum__media .swiper-wrapper {
  @media (min-width: 1024px) {
    position: static;
    transform: none !important;
  }
}

.final-forum__media-item {
  position: absolute;
  z-index: 1;
  padding-right: 0;
  transition: 0.1s transform;
  will-change: transform;

  img {
    vertical-align: top;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &--left-top {
    top: 55px;
    left: -30px;
    width: 340px;
    height: 239px;
  }

  &--left-mid {
    top: 360px;
    left: -275px;
    width: 192px;
    height: 192px;
  }

  &--left-bottom {
    bottom: 65px;
    left: -142px;
    width: 298px;
    height: 217px;
  }

  &--right-top {
    top: 112px;
    right: -310px;
    width: 357px;
    height: 225px;
  }

  &--right-bottom {
    bottom: 55px;
    right: -110px;
    width: 271px;
    height: 205px;
  }

  &--mid-bottom {
    bottom: 20px;
    left: calc(50% - 162px);

    width: 325px;
    height: 205px;
  }

  @include laptop-only {
    &--left-mid {
      left: -100px;
      top: 400px;
    }

    &--left-bottom {
      left: -20px;
    }

    &--right-top {
      right: -210px;
    }

    &--right-bottom {
      right: -50px;
    }
  }

  @include tablet {
    position: relative;
    left: auto;
    top: auto;
    right: auto;
    bottom: auto;
    width: auto;
    height: auto;

    flex-shrink: 0;

    border-radius: 6px;
    overflow: hidden;

    margin-right: 16px;

    img {
      width: auto;
      height: 163px;
    }

    &--mid-bottom {
      padding-top: 80px;

      img {
        width: 257px;
        height: auto;
      }
    }
  }
}

