.bubble {
  position: absolute;
  bottom: 112px;
  left: 50%;
  z-index: 100;

  cursor: pointer;

  @media (min-width: 1440px) {
    bottom: calc(10vh + 20px);
  }

  @include desktop-2k {
    bottom: calc(9vh + 20px);
  }
}

.bubble__text-wrap {
  display: block;
  transition: transform $aos-duration-transform linear, opacity $aos-duration-opacity linear;
}

.bubble__text {
  display: block;
  min-width: 160px;
  position: relative;

  padding: 12px 36px;
  padding-top: 17px;

  font-family: $font-commons;
  font-size: 19px;
  line-height: 18px;
  text-align: center;
  color: $color-secondary;

  border: none;
  border-radius: 30px;
  background-color: $white;
  box-shadow: $bubble-shadow;

  animation: bounce-bubble 30s infinite linear;
  animation-delay: 15s;

  @include laptop {
    padding: 9px 40px;
    padding-top: 13px;

    font-family: $font-commons;
    font-size: 17px;
    line-height: 20px;
  }
}

.bubble--right .bubble__text::before {
  content: "";

  position: absolute;
  bottom: -24px;
  left: 22px;

  width: 32px;
  height: 24px;

  background-image: url("../img/bubble-top.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  filter: drop-shadow($bubble-drop-shadow);

  transform: rotate(0) scale(1);
}

.bubble--right::after {
  content: "";

  position: absolute;
  bottom: -78px;
  left: -24px;

  width: 48px;
  height: 48px;

  border: none;
  border-radius: 50%;

  background-color: $color-primary;
}

.bubble.bubble--left {
  z-index: 100;
  left: calc(50% - 162px);
  bottom: 145px;
}

.bubble.bubble--third {
  bottom: calc(12% + 20px);

}

.bubble--left .bubble__text::before {
  content: "";

  position: absolute;
  bottom: -24px;
  right: 22px;

  width: 32px;
  height: 24px;

  background-image: url("../img/bubble-top.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transform: rotate(0) scale(-1, 1);
  filter: drop-shadow($bubble-drop-shadow);
}

.bubble--left::after {
  content: "";

  position: absolute;
  bottom: -70px;
  right: -31px;

  width: 48px;
  height: 48px;

  border: none;
  border-radius: 50%;

  background-color: $color-primary;
}

.bubble svg {
  position: absolute;
  top: 87px;
  left: -11px;
  z-index: 100;

  width: 22px;
  height: 28px;

  animation: bounce 2s infinite linear;

  @include laptop {
    top: 73px;
  }
}

.bubble--left svg {
  position: absolute;
  top: 81px;
  left: 156px;

  @include laptop {
    top: 87px;
    left: -11px;
  }
}

.bubble--down svg {
  position: absolute;
  top: -68px;
  left: -11px;

  @include laptop {
    top: -67px;
  }
}

@include laptop {
  .bubble::after {
    bottom: -66px;
    left: -24px;
  }
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }

  25% {
    transform: translateY(-2px);
  }

  75% {
    transform: translateY(2px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes bounce-bubble {
  0%,
  47%,
  53%,
  100% {
    transform: translateY(0);
  }

  48.5% {
    transform: translateY(-5px);
  }

  51.5% {
    transform: translateY(5px);
  }
}
