.schedule {
  overflow: hidden;
  color: $black-dark;
}

.schedule__container {
  @include container;
  padding-top: 56px;

  @include mobile {
    padding-top: 51px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.schedule__title,
.schedule__period-title {
  margin: 0;
  font-family: $font-commons;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 28px;
  line-height: 1;
  padding-left: 100px;
  padding-right: 100px;

  @include mobile {
    font-size: 24px;
    padding-left: 88px;
    padding-right: 88px;

    color: #0B1051;
  }
}

.schedule__title {
  margin-bottom: 22px;
  text-align: center;

  @include mobile {
    padding: 0;
    text-align: center;
    margin-bottom: 22px;
  }
}

.schedule__period {
  padding-bottom: 48px;

  &.swiper-slide {
    pointer-events: none;
    opacity: 0!important;
  }

  &.swiper-slide-active {
    opacity: 1!important;
    pointer-events: auto;
  }

  @include mobile {
    padding-bottom: 32px;
  }
}

.schedule__period-title {
  padding-left: 90px;
  padding-right: 90px;
  margin: 0 auto 50px;
  width: 326px;
  text-align: center;

  @include mobile {
    padding-left: 75px;
    padding-right: 75px;
    width: auto;
    margin-bottom: 43px;
  }
}

.schedule__items {
  display: flex;
  align-items: center;
  min-height: 383px;

  .swiper-wrapper {
    align-items: center;
  }

  @include mobile {
    min-height: auto;
  }
}

.schedule__slider {
  position: relative;
}

.schedule__controls {
  position: relative;
  width: 326px;
  left: 0;
  top: -4px;
  outline: none;
  margin: 0 auto;

  @include mobile {
    width: 100%;
  }
}

.schedule__controls-btn {
  @include btn-reset;
  background: url("../img/make-biz/icon-arrow-prev.png") no-repeat;
  background-size: 100%;
  width: 74px;
  height: 33px;

  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;

  transition: opacity 0.3s;
  outline: none;

  &:focus,
  &:focus-visible {
    outline: 0;
  }

  &.swiper-button-disabled {
    opacity: 0.1;
  }

  @include mobile {
    background-image: url("../img/make-biz/icon-arrow-prev-blue.png");
  }

  &--next {
    left: auto;
    right: 0;
    background-image: url("../img/make-biz/icon-arrow-next.png");

    @include mobile {
      left: auto;
      right: 0;

      background-image: url("../img/make-biz/icon-arrow-next-blue.png");
    }
  }

  @include mobile {
    width: 64px;
  }
}
