.nd-vkbiz-user-block {
  display: flex;
  align-items: center;
  padding: 3px 16px;
  letter-spacing: -0.5px;
  border-radius: 17px;
  transition: background-color 0.3s ease;
}

.nd-vkbiz-user-block a {
  display: inline-block;
  vertical-align: top;
}

.nd-vkbiz-user-block__image {
  flex: none;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  overflow: hidden;
}

.nd-vkbiz-user-block__image img {
  display: block;
  width: 100%;
  height: auto;
}

.nd-vkbiz-user-block__label {
  margin: 0;
  color: #4680c2;
}

.nd-vkbiz-user-block__label:hover {
  text-decoration: none;
}

.nd-vkbiz-user-block:hover {
  background-color: #f6f8f9;
}

@media (max-width: 879px) {
  .nd-vkbiz-user-block__label {
    margin-left: 11px;
  }
}
