.step {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 0 auto;
  padding-top: 100px;
  padding-bottom: 100px;
  height: 100vh;
  min-height: 800px;

  &--fourth {
    min-height: 730px;
    height: calc(100vh - 70px);
  }

  @include laptop {
    margin: 0 auto;

    padding-top: 120px;

    height: auto;
    min-height: auto;
    max-height: auto;
    padding-bottom: 0;
  }

  @media (min-height: 900px) {
    justify-content: center;
  }
}

.step--white {
  background-repeat: no-repeat;
  background-size: 100% 100%;

  @include laptop {
    min-height: 300px;

    padding-top: 164px;

    background-image: url("../img/first-wave--mobile.svg");
    background-repeat: no-repeat;
  }

  &.step--third {
    @include laptop {
      min-height: 782px;

      background-image: url("../img/second-wave--mobile.svg");
      background-repeat: no-repeat;
    }
  }
}

.step__wrapper {
  position: relative;
  z-index: 2;

  display: flex;

  margin: 0 auto;

  @include laptop {
    flex-direction: column;
    align-items: center;

    margin-bottom: 75px;
  }
}

.step__column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include laptop {
    align-items: center;
  }

  &:first-of-type {
    width: 550px;

    margin-right: 55px;
    margin-left: 73px;

    @include laptop {
      width: 320px;

      margin: 0;
      margin-bottom: 50px;
    }
  }
}

.step__counter {
  position: absolute;
  top: 15px;
  left: -32px;

  display: inline-block;

  width: 38px;
  height: 38px;

  margin-right: 35px;

  border-radius: 50%;

  background-color: $color-secondary;

  font-family: $font-commons;
  font-weight: 700;
  font-size: 30px;
  line-height: 44px;
  text-align: center;
  vertical-align: middle;
  color: $white;

  @include laptop {
    top: -58px;
    left: 46%;

    width: 25px;
    height: 25px;

    font-size: 21px;
    line-height: 30px;
  }

  &::before {
    content: "";

    position: absolute;
    top: -11px;
    left: -11px;
    z-index: -1;

    width: 60px;
    height: 60px;

    border-radius: 50%;

    background-color: $color-secondary;

    opacity: 0.6;

    transition: transform 0.3s;

    @include laptop {
      top: -8px;
      left: -8px;

      width: 40px;
      height: 40px;
    }
  }

  &::after {
    content: "";

    position: absolute;
    top: -24px;
    left: -24px;
    z-index: -2;

    width: 87px;
    height: 87px;

    border-radius: 50%;

    background-color: $color-secondary-light;

    transition: transform 0.3s;

    @include laptop {
      top: -17px;
      left: -17px;

      width: 58px;
      height: 58px;
    }
  }

  &:hover::before,
  &:hover::after {
    transform: scale(1.1);
  }
}

.step__title {
  margin: 0;
  margin-bottom: 8px;
  padding: 0;

  font-family: $font-commons;
  font-weight: 700;
  font-size: 70px;
  line-height: 1;
  color: $white;
  text-align: left;

  @include laptop {
    max-width: 267px;

    margin-bottom: 5px;

    font-size: 28px;
    text-align: center;
  }
}

.step--white .step__title {
  color: $color-primary-dark;
}

.step__text {
  max-width: 500px;

  margin-bottom: 35px;

  font-family: $font-text;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.5;
  color: $white;
  text-align: left;

  @include laptop {
    max-width: 291px;

    margin-bottom: 33px;

    font-size: 14px;
    text-align: center;
  }
}

.step--white .step__text {
  color: $black;
}

.step__list {
  display: block;

  margin: 0;
  padding: 0;

  list-style: none;

  @include laptop {
    align-self: flex-start;
  }
}

// .step__list--arrows {
//   margin-left: 25px;
//
//   @include laptop {
//     margin-left: 0;
//   }
// }

.step__item {
  position: relative;

  margin: 0;
  margin-bottom: 10px;
  padding: 0;

  a {
    color: inherit;
    text-decoration: none;
  }

  @include laptop {
    font-size: 14px;
    line-height: 1.25;
  }
}

.step__list--checks .step__item {
  padding-left: 30px;

  @include laptop {
    padding-left: 20px;
  }

  @include min-width {
    padding-left: 30px;
  }

  &::before {
    content: "";

    position: absolute;
    top: 10px;
    left: 0;

    width: 16px;
    height: 14px;

    background-image: url("../img/check.svg");
    background-repeat: no-repeat;
    background-position: center;

    @include laptop {
      top: 4px;
      left: 0;

      width: 12px;
      height: 10px;
      background-size: 12px 10px;
    }

    @include min-width {
      left: 10px;
    }
  }
}

.step__list--arrows .step__item {
  cursor: pointer;

  transition: transform 0.3s;

  @include desktop-only {
    &:hover,
    &:focus {
      transform: translateX(10px);
    }
  }

  @include min-width {
    padding-left: 5px;
  }

  &::after {
    content: "";

    display: inline-block;

    width: 16px;
    height: 9px;

    margin-left: 13px;

    background-image: url("../img/toRight.svg");
    background-repeat: no-repeat;
    background-position: center;

    @include laptop {
      top: 7px;
      right: 0;

      width: 7px;
      height: 7px;
    }
  }
}

.step__video {
  position: relative;
  z-index: 0;

  margin-top: -20px;
  margin-bottom: 35px;
  width: 452px;
  height: 452px;

  border-radius: 10px;
  overflow: hidden;

  background-color: $gray;

  @include laptop {
    width: 323px;
    height: 323px;
  }

  @include laptop {
    margin-bottom: 24px;
  }

  @include min-width {
    width: 310px;
    height: 310px;
  }
}

.step__video iframe {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
}


.step__video img {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  display: block;

  width: 100%;
  height: 100%;
  object-fit: cover;

  border-radius: 10px;
}

.step__links {
  display: flex;

  margin-top: 0;

  @include laptop {
    display: flex;
    justify-content: flex-start;

    width: 322px;
  }

  @include min-width {
    width: 310px;
  }
}

.step--fourth .step__links {
  @include laptop {
    display: flex;
    justify-content: center;
  }
}

.step__more,
.step__question {
  position: relative;

  padding-left: 37px;

  font-family: $font-commons;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.25;
  color: $color-primary-dark;
  text-align: left;

  transition: color 0.3s linear;

  @include laptop {
    padding-left: 28px;

    font-size: 16px;
  }

  &:hover,
  &:focus {
    transition-duration: 0.1s;
    color: $color-secondary-light;
  }
}

.step--blue .step__more,
.step--blue .step__question {
  color: $white;

  &:hover,
  &:focus {
    color: $color-primary-dark;
  }
}

.step__more {
  max-width: 215px;

  margin-right: 40px;

  @include laptop {
    max-width: 158px;

    margin-right: 42px;
  }
}

.step__more svg,
.step__question svg {
  position: absolute;
  top: -5px;
  left: 0;

  width: 31px;
  height: 29px;

  fill: currentColor;

  @include laptop {
    top: -5px;
    left: 0;

    width: 25px;
    height: 24px;
  }
}

.step__question {
  max-width: 162px;

  padding-left: 40px;

  @include laptop {
    max-width: 118px;

    padding-left: 28px;
  }
}

.step__button {
  padding: 19px 45px;
  padding-right: 60px;
  min-width: 173px;

  background: $color-primary url("../img/toRight.svg") no-repeat right 35px center;

  @include laptop {
    padding: 14px 34px;
    padding-right: 50px;
    min-width: 201px;
    background-size: 7px 12px;
    background-position: right 22px center;
  }
}

.step__hint {
  @include laptop {
    display: none;
  }
}

.step__hint .bubble__text {
  border: 1px solid $light-gray;
}

.step__line-wrap {
  position: absolute;
  z-index: 1;
  top: calc(((100% - 494px) / 2) + 20px);
  bottom: calc(-1 * (((100% - 494px) / 2) + 20px));
  left: calc(50% - 640px);
  width: 133px;
  overflow: hidden;
  animation: dash-wrap 3s linear forwards;
  animation-delay: 0.5s;
  clip-path: inset(0 0 100% 0);
  animation-play-state: paused;

  @include laptop {
    display: none;
  }
}

.section--active {
  .step__line-wrap {
    animation-play-state: running;
  }
}

.step__line-wrap::before {
  content: "";
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: url("../img/line-1.svg") no-repeat;
  background-size: 50% 100%;
}

@keyframes dash-wrap {
  to {
    clip-path: inset(0);
  }
}

.section:nth-child(2n + 1) {
  .step__line-wrap:not(.step__line-wrap--final) {
    left: calc(50% - 650px);
    transform: scale(-1, 1);
  }
}


.step__line-wrap--final {
  bottom: 150px;
  width: 590px;
  left: calc(50% - 590px);

  &::before {
    background-image: url("../img/arrow-dash.svg");
    background-size: auto 100%;
    background-position: left bottom;
  }

  @include desktop-large {
    top: calc(50vh - 200px);
    left: calc(50% - 570px);
  }
}
