.tabs__nav {
  @include list-reset;
  border-radius: 19px;

  display: inline-flex;
  align-items: center;
  background: rgb(234, 238, 245);
}

.tabs__nav-link {
  display: block;
  color: $color-blue;
  border-radius: 19px;

  font-size: 16px;
  line-height: 18px;
  padding: 10px 24px;

  background: rgb(234, 238, 245);

  &--active {
    background: $color-blue;
    color: $white;
  }
}

.tabs__content-item {
  &--hide {
    display: none;
  }
}
