.contest {
  @include container;
  background: #f8fafd;
  border-radius: 20px;
  padding: 37px;
  text-align: center;
  max-width: 1000px;
  margin-bottom: 50px;

  &--hide {
    display: none;
  }

  @include mobile {
    padding: 32px 16px;
    border: none;
    border-radius: 0;
    margin-bottom: 8px;
  }
}

.contest__title {
  @include subtitle;
  margin: 0 0 8px;
}

.contest__call {
  margin: 0 0 5px;
  font-family: $font-commons;
  font-size: 28px;
  line-height: 1.3;
  color: $black;

  strong {
    font-weight: 600;
  }

  @include mobile {
    font-size: 24px;
    line-height: 1.2;
    margin-bottom: 13px;
    color: #0B1051;
  }
}

.contest__desc {
  font-size: 16px;
  line-height: 1.3;
  padding: 0;
  margin-bottom: 32px;

  p {
    margin: 0;
  }

  @include mobile {
    font-size: 14px;
    margin-bottom: 35px;
  }
}

.contest__call a,
.contest__desc a {
  @include text-link;
}

.contest__members-wrap {
  display: flex;
  align-items: center;
  justify-content: center;

  @include mobile {
    display: block;
  }
}

.contest__members {
  margin-right: 36px;

  @include mobile {
    transform: translateX(-7px);
    margin-right: 0;
    margin-bottom: 20px;
  }
}

