.modal {
  position: fixed;
  z-index: 9999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  cursor: pointer;
  background: rgba(#17191b, 0.7);

  display: none;

  padding: 82px 95px;

  &--show {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @include mobile {
    padding: 60px 30px;
  }
}

.modal__content {
  flex-grow: 1;
  width: 100%;
  height: 100%;
  max-width: 1250px;
  max-height: 700px;

  cursor: auto;

  iframe {
    display: block;
    width: 100%;
    height: 100%;
    border: none;
  }

  @include mobile {
    max-height: 400px;
  }
}
