.section {
  position: relative;
  background-repeat: repeat-x;

  &--transition {
    transition: background-position 0.5s;
  }

  &:nth-child(even) {
    background-position: top center;
    background-size: cover;
  }

  &:nth-child(odd) {
    background-position: bottom center;
    background-size: contain;
  }

  &:first-child {
    background-image: url("../img/intro.svg");
    background-size: calc(100% + 340px) 135px;
    background-position: left calc(50% - 120px) bottom -2px;

    height: 100vh;
    min-height: 800px;

    @include tablet {
      height: auto;
      min-height: auto;
    }
  }

  &:nth-child(4n+2) {
    background-image: url("../img/step1.svg");

    background-size: calc(100% + 340px) 100%;
    background-position: calc(50% + 120px) bottom;
  }

  &:nth-child(4n+3) {
    background-image: url("../img/step2.svg");

    background-size: calc(100% + 180px) 103px;
    background-position: calc(50% + 40px) bottom;
  }

  &:nth-child(4n+4) {
    background-image: url("../img/step3.svg");

    background-size: calc(100% + 240px) 100%;
    background-position: calc(50% - 70px) bottom;
  }

  &:nth-child(4n+5) {
    background-image: url("../img/step4.svg");
    background-size: calc(100% + 220px) 89px;
    background-position: calc(50% + 60px) bottom;
  }

  &:last-child:nth-child(even) {
    background-image: none;
  }

  @include laptop {
    &:nth-child(n) {
      background-image: none;
    }
  }
}
