.main-footer {
  background: $white;
  color: $gray;

  font-size: 17px;
  line-height: 1.5;

  padding: 25px 59px 30px;
  padding-right: 55px;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  width: 100%;

  @include mobile {
    font-size: 14px;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
  }
}

.main-footer__copyright {
  position: relative;
  z-index: 1;
  padding-right: 29px;
  margin-right: 20px;

  &::after {
    content: "";
    position: absolute;
    z-index: 1;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 22px;
    height: 19px;
    background: url("../img/heart.svg") no-repeat;
    background-size: 100%;
  }

  @include mobile {
    margin-right: 0;
    margin-bottom: 13px;
    padding-right: 20px;

    &::after {
      width: 15px;
      height: 13px;
    }
  }
}

.main-footer__question {
  color: inherit;
  transition: color 0.3s;

  &:hover,
  &:focus {
    color: $color-primary-dark;
  }
}
