.video-btn {
  @include btn-reset;

  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: url("../img/make-biz/ico_play.png") no-repeat;
  background-size: 100% 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  outline: none;

  @include mobile {
    width: 53px;
    height: 53px;
  }
}
