ul.nd-vkbiz-subnav {
  padding: 0;
  margin: 0;
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 8px;
  list-style: none;
  box-sizing: border-box;
  min-width: 207px;
  text-align: left;
  background-color: #ffffff;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease;
  overflow: hidden;
}

ul.nd-vkbiz-subnav:hover {
  opacity: 1;
  pointer-events: auto;
}

ul.nd-vkbiz-subnav_illustrated {
  margin: 8px 0 0;
  padding: 16px;
  list-style: none;
  min-width: 387px;
}

.nd-vkbiz-subnav_illustrated li.nd-vkbiz-subnav__item {
  margin-bottom: 16px;
}

.nd-vkbiz-subnav_illustrated li.nd-vkbiz-subnav__item:last-child {
  margin-bottom: 0;
}

a.nd-vkbiz-subnav-link {
  display: flex;
  align-items: center;
  color: #4680c2;
}

a.nd-vkbiz-subnav-link:hover {
  text-decoration: none;
}

.nd-vkbiz-subnav-link__icon {
  flex: none;
  display: block;
  margin-right: 16px;
  width: 68px;
  height: 68px;
  border-radius: 6px;
  overflow: hidden;
}

.nd-vkbiz-subnav-link__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.nd-vkbiz-subnav-link__icon img {
  display: block;
  width: 100%;
  height: auto;
}

.nd-vkbiz-subnav-link__descr {
  margin: 2px 0 0;
  font-size: 14px;
  line-height: 17px;
  color: #828282;
  letter-spacing: 0.3px;
}

.nd-vkbiz-subnav-link__headline {
  margin-bottom: 3px;
  letter-spacing: 0.3px;
}

.nd-vkbiz-subnav-link__headline span {
  border-bottom: 1px solid rgba(255, 255, 255, 0);
  transition: border-color 0.3s ease;
}

.nd-vkbiz-subnav-link:hover .nd-vkbiz-subnav-link__headline span {
  border-color: #4680c2;
}

.nd-vkbiz-subnav__link_plain {
  display: flex;
  align-items: center;
  padding: 11px 16px;
  line-height: 26px;
  color: #4680c2;
  letter-spacing: 0.5px;
  transition: background-color 0.3s ease;
}

.nd-vkbiz-subnav__link_plain:hover {
  background-color: #f6f8f9;
  text-decoration: none;
}

.nd-vkbiz-subnav__link-bullet {
  display: block;
  margin-right: 8px;
  width: 26px;
  height: 26px;
}

@media (max-width: 1039px) {
  .nd-vkbiz-subnav__link_plain {
    letter-spacing: 0.5px;
  }

  ul.nd-vkbiz-subnav {
    min-width: 218px;
  }

  ul.nd-vkbiz-subnav_illustrated {
    min-width: 387px;
  }
}

@media (max-width: 879px) {
  ul.nd-vkbiz-subnav {
    position: static;
    box-sizing: border-box;
    top: 0;
    margin: 0;
    width: 100%;
    max-width: 100%;
    opacity: 1;
    pointer-events: auto;
    border-radius: 0;
    box-shadow: none;
  }

  ul.nd-vkbiz-subnav_illustrated {
    position: fixed;
    top: 45px;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
    min-width: 100%;
    max-width: 100%;
    overflow: hidden;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    box-sizing: border-box;
    padding: 16px 10px 16px 16px;
    max-height: calc(100vh - 56px);
    overflow-y: auto;
    z-index: 1;
  }

  ul.nd-vkbiz-subnav.show {
    transform: translateX(0);
  }

  .nd-vkbiz-nav__back-link:not(.hidden) {
    display: flex;
    align-items: center;
    background-color: #ffffff;
    z-index: 1;
  }

  a.nd-vkbiz-subnav-link {
    align-items: flex-start;
  }

  .nd-vkbiz-subnav-link__icon {
    width: 44px;
    height: 44px;
  }

  p.nd-vkbiz-subnav-link__headline {
    margin-bottom: 2px;
    font-size: 14px;
    line-height: 17px;
  }

  .nd-vkbiz-subnav-link__descr {
    font-size: 12px;
    line-height: 14px;
  }

  .nd-vkbiz-subnav__link-bullet {
    margin-right: 11px;
  }
}
